import React, { useEffect, useState } from "react";
import axios from "axios";
import FullCalendar from "@fullcalendar/react";
import dayGridPlugin from "@fullcalendar/daygrid";
import timeGridPlugin from "@fullcalendar/timegrid";
import interactionPlugin from "@fullcalendar/interaction";
import "@fullcalendar/daygrid/main.css";
import styled from "styled-components";

//calendar css
export const StyleWrapper = styled.div`
  .fc td {
    color:black;
  }
`

function ReservationsCalendar() {
  const [Reservation, setReservation] = useState([]);

  useEffect(() => {
    axios
      .post("/api/reservation/getList")
      .then((response) => {
        if (response.data.success) {
          setReservation(response.data.doc);
        } else {
          console.log("");
        }
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  const events = Reservation.map((item) => {

    let selectday = item.selectDay[0];

    //  format : `${selectday.year}-${selectday.month}-${selectday.day}` => ex. 2020-08-06
    // {year:2020, day:6, month:8}   
    // 6 -> 06, 8->08    m1, m2
    // ** parseInt -> "06" string parseInt   6  ..
    if(selectday.day < 10) {
      selectday.day = '0'+selectday.day
    }
    if(selectday.month < 10) {
      selectday.month = '0'+selectday.month
    }
    let reservationdate = `${selectday.year}-${selectday.month}-${selectday.day}`

    return (
      {
        title: item.title,
        date: reservationdate,
      }
    )
  });

  return (
    <StyleWrapper>

      <FullCalendar
        defaultView="dayGridMonth"
        header={{
          left: "prev,next today",
          center: "title",
          right: "dayGridMonth,timeGridWeek,timeGridDay,listWeek",
        }}
        style={{ color: "black" }}
        plugins={[dayGridPlugin, timeGridPlugin, interactionPlugin]}
        events={events}
      />
    </StyleWrapper>
  );
}

export default ReservationsCalendar;