import React, { useState, useEffect } from "react";
import { Table, PageHeader, Tag } from "antd";
import axios from "axios";
const { Column } = Table;

function ReservationList() {
  const [Reservation, setReservation] = useState([]);
  useEffect(() => {
    axios
      .post("/api/reservation/getList")
      .then(response => {
        if (response.data.success) {
          setReservation(response.data.doc);
        } else {
          alert("");
        }
      })
      .catch(err => {
        console.log(err);
      });
  }, []);
  return (
    <PageHeader
      title="Reservation List"
      className="site-page-header"
      tags={<Tag color="blue"></Tag>}
      avatar={{
        src: "https://avatars1.githubusercontent.com/u/8186664?s=460&v=4",
      }}
    >
      <div style={{ width: "80%", margin: "3rem auto" }}>
        <div style={{ textAlign: "center" }}>
          <br />

          <Table dataSource={Reservation}>
            <Column title="" dataIndex="id" key="id" />
            <Column title="" dataIndex="title" key="title" />
            <Column
              title=""
              dataIndex="selectDay"
              key="selectDay"
              render={selectDay => (
                <>
                  {selectDay.map((item, index) => (
                    <Tag key={index}>
                      {item.year}.{item.month}.{item.day}
                    </Tag>
                  ))}
                </>
              )}
            />
            <Column
              title=""
              dataIndex="time"
              key="time"
              render={time => (
                <>
                  {time.map((item, index) => (
                    <Tag key={index}>{item.time}</Tag>
                  ))}
                </>
              )}
            />
            <Column title="" dataIndex="continent" key="continent" />
            <Column title="" dataIndex="theater" key="theater" />
            <Column
              title=""
              dataIndex="seat"
              key="seat"
              render={(
                Seat //Seat:  
              ) =>
                Seat.map((seat, index) => {
                  if (index < Seat.length - 1) {
                    return seat + ", ";
                  } else {
                    return seat;
                  }
                })
              }
            />
            <Column title="" dataIndex="price" key="price" />
          </Table>
        </div>
      </div>
    </PageHeader>
  );
}

export default ReservationList;
