import React, { useState, useEffect } from "react";
import { Table, PageHeader, Tag, Space, Button } from "antd";

import axios from "axios";
import { auth } from "../../../_actions/user_action";
import { useDispatch } from "react-redux";
const { Column } = Table;

function UserList() {
  const dispatch = useDispatch();
  const [User, setUser] = useState([]);

  useEffect(() => {
    getUser();
  }, []);

  const getUser = () => {
    axios.get("/api/users/management").then((response) => {
      if (response.data.success) {
        setUser(response.data.users);
      } else {
        alert(" ");
      }
    });
  };

  // -> 
  const onRoleAdminHandler = (_id) => {
    const variables = {
      _id,
    };
    axios.post("/api/users/roleAdmin",variables).then((response) => {
      if (response.data.success) {
        alert(" ");
        getUser();
        dispatch(auth());
      } else {
        alert("");
      }
    });
  };
  // -> 
  const onRoleUserHandler = (_id) => {
    const variables = {
      _id,
    };
    axios.post("/api/users/roleUser",variables).then((response) => {
      if (response.data.success) {
        alert(" ");
        getUser();
        dispatch(auth());
      } else {
        alert("");
      }
    })
  };

  const onUserDeleteHandler = (_id) => {
    const variables = {
      _id,
    };

    axios.post("/api/users/removeFromUser", variables).then((response) => {
      if (response.data.success) {
        alert(" ")
        getUser();
      } else {
        alert("  .");
      }
    });
  };

  return (
    <PageHeader
      title="User List"
      className="site-page-header"
      subTitle=" "
      tags={<Tag color="blue"></Tag>}
      avatar={{
        src: "https://avatars1.githubusercontent.com/u/8186664?s=460&v=4",
      }}
    >
      <div style={{ width: "80%", margin: "3rem auto" }}>
        <div style={{ textAlign: "center" }}>
          <br />

          <Table dataSource={User}>
            <Column title="" dataIndex="name" key="name" />
            <Column title="" dataIndex="email" key="email" />
            <Column title="" dataIndex="role" key="role" />
            <Column
              title=""
              key="action"
              dataIndex="_id"
              render={(dataIndex) => (
                <Space size="middle">
                  <Button
                    type="primary"
                    style={{
                      backgroundColor: "#9c88ff",
                      borderColor: "#9c88ff",
                    }}
                    // danger
                    onClick={() => onRoleAdminHandler(dataIndex)}
                  >
                    
                  </Button>
                  <Button type="primary" onClick={() => onRoleUserHandler(dataIndex)}>
                    
                  </Button>
                </Space>
              )}
            />
            <Column
              title=""
              key="action"
              dataIndex="_id"
              render={(dataIndex) => (
                <Space size="middle">
                  <Button
                    ghost
                    type="primary"
                    danger
                    style={{ border: "1.5px solid" }}
                    onClick={() => onUserDeleteHandler(dataIndex)}
                  >
                    <span style={{ color: "red" }}></span>
                  </Button>
                </Space>
              )}
            />
          </Table>
        </div>
      </div>
    </PageHeader>
  );
}

export default UserList;
