import axios from "axios";
import {
  REGISTER_USER,
  LOGIN_USER,
  AUTH_USER,
  ADD_TO_MOVIE,
  GET_MOVIE_ITEMS,
  REMOVE_MOVIE_ITEM,
  ADD_TO_CART,
  GET_CART_ITEMS,
  REMOVE_CART_ITEM,
  ON_SUCCESS_BUY,
  REMOVE_PRODUCT,
} from "./types";

export function loginUser(dataToSubmit) {
  //node  
  const request = axios
    .post("/api/users/login", dataToSubmit)
    .then((response) => response.data);

  return {
    //request reducer  
    type: LOGIN_USER,
    payload: request,
  };
}

export function registerUser(dataToSubmit) {
  //node  
  const request = axios
    .post("/api/users/register", dataToSubmit)
    .then((response) => response.data);

  return {
    //request reducer  
    type: REGISTER_USER,
    payload: request,
  };
}

//get body .
export function auth() {
  //node  
  const request = axios
    .get("/api/users/auth")
    .then((response) => response.data);

  return {
    //request reducer  
    type: AUTH_USER,
    payload: request,
  };
}

export function addToMovie(objectId) {
  let body = {
    objId: objectId,
  };

  //node  
  const request = axios
    .post("/api/users/addToMovie", body)
    .then((response) => response.data);

  return {
    //request reducer  
    type: ADD_TO_MOVIE,
    payload: request,
  };
}

export function addToCart(id) {
  let body = {
    productId: id,
  };

  const request = axios
    .post("/api/users/addToCart", body)
    .then((response) => response.data);

  return {
    //request reducer  
    type: ADD_TO_CART,
    payload: request,
  };
}

export function getMovieItems(movieObjIds, userMovie) {
  //node  
  const request = axios
    .get(`/api/reservation/reservation_by_id?id=${movieObjIds}&type=array`)
    .then((response) => {
      return response.data;
    });

  return {
    //request reducer  
    type: GET_MOVIE_ITEMS,
    payload: request,
  };
}

export function getCartItems(cartItems, userCart) {
  const request = axios
    .get(`/api/product/products_by_id?id=${cartItems}&type=array`)
    .then((response) => {
      //CartItem  
      //product collection 
      //Quantity   .
      userCart.forEach((cartItem) => {
        response.data.forEach((productDetail, index) => {
          if (cartItem.id === productDetail._id) {
            response.data[index].quantity = cartItem.quantity;
          }
        });
      });
      return response.data;
    });

  return {
    //request reducer  
    type: GET_CART_ITEMS,
    payload: request,
  };
}

export function removeMovieItem(movieObjId) {
  //node  
  const request = axios
    .get(`/api/users/removeFromMovie?id=${movieObjId}`)
    .then((response) => {
      return response.data;
    });

  return {
    //request reducer  
    type: REMOVE_MOVIE_ITEM,
    payload: request,
  };
}

export function removeCartItem(productId) {
  //node  
  const request = axios
    .get(`/api/users/removeFromCart?id=${productId}`)
    .then((response) => {
      //productInfo, cart   CartDetail .
      response.data.cart.forEach((item) => {
        response.data.productInfo.forEach((product, index) => {
          if (item.id === product._id) {
            response.data.productInfo[index].quantity = item.quantity;
          }
        });
      });

      return response.data;
    });

  return {
    //request reducer  
    type: REMOVE_CART_ITEM,
    payload: request,
  };
}

export function onSuccessBuy(data) {
  //node  
  const request = axios
    .post(`/api/users/successBuy`, data)
    .then((response) => response.data);

  return {
    //request reducer  
    type: ON_SUCCESS_BUY,
    payload: request,
  };
}

