import React, { useState, useEffect } from "react";
import axios from "axios";
import styled from "styled-components";
import "../Reservation/style.css";
import {
  Continents,
  SeatA2,
  SeatB2,
  SeatC2,
  SeatD2,
  SeatE2,
  SeatF2,
  SeatG2,
  SeatF,
  SeatG,
  SeatE,
  SeatD,
  SeatA,
  SeatB,
  SeatC,
} from "../Reservation/Modal/Context";
import { useDispatch } from "react-redux";
import { addToMovie } from "../../_actions/user_action";
import Select from "react-select";
import { IMAGE_BASE_URL, CLIENT } from "../../Components/Config";

const PriceTag = styled.div`
  font-size: 20px;
  font-weight: 30px;
`;
const Small = styled.div`
  font-size: 20px;
  color: white;
`;
const Wrapper = styled.div`
  float: left;
  height: 100%;
`;
const SeatWrapper = styled.div`
  float: right;
  height: 100%;
`;
const SelectWrapper = styled.div`
  background-color: #242333;
  color: #fff;
  font-family: "Lato", sans-serif;
`;
const InnerWrapper = styled.div`
  background-color: #242333;
  color: #fff;
  font-family: "Lato", sans-serif;
`;
const Cover = styled.div`
  width: 90%;
  height: 100%;
  background-image: url(${props => props.bgImage});
  background-position: center center;
  background-size: cover;
  border-radius: 5px;
  box-shadow: 2px 6px 20px 0 rgba(0, 0, 0, 0.65);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
`;
function Booking({ id, title, bgImage, userFrom, selectDay, time, theater }) {
  const dispatch = useDispatch();
  const [Continent, setContinent] = useState(0);
  const [Seat, setSeat] = useState([]);
  const [Price, setPrice] = useState(0);
  const [Distinct, setDistinct] = useState([]);

  const movieTitle = {
    title: title,
  };
  useEffect(() => {
    axios
      .post("/api/reservation/findSeat", movieTitle)
      .then(response => {
        if (response.data.success) {
          let seatlist = [];
          response.data.seats.forEach(obj => {
            if (
              obj.time[0].time === time.time &&
              obj.selectDay[0].day === selectDay.day &&
              obj.selectDay[0].month === selectDay.month &&
              obj.selectDay[0].year === selectDay.year &&
              obj.theater === theater
            ) {
              seatlist.push(obj.seat);
            }
          });
          const flatlist = seatlist.flat(); // !!! from 
          setDistinct(flatlist);
        }
      })
      .catch(err => {
        console.log(err);
      });
      //[]  warning
  }, [movieTitle, selectDay, theater, time]);
  //seat  
  const onSeatChange = e => {
    if (Continent >= Seat.length + 1) {
      //   
      if (
        //
        !e.target.classList.contains("occupied") &&
        !e.target.classList.contains("selected")
      ) {
        e.target.classList.add("selected"); // 
        setSeat([...Seat, e.target.textContent]); //
      } else if (e.target.classList.contains("selected")) {
        //  
        // 
        e.target.classList.remove("selected");
        const SeatFiltered = Seat.filter(seat => seat !== e.target.textContent); //text
        setSeat(SeatFiltered);
      }
    } else {
      if (Continent === Seat.length) {
        e.target.classList.remove("selected");
        const SeatFiltered = Seat.filter(seat => seat !== e.target.textContent); //text
        setSeat(SeatFiltered);
        if (!Seat.includes(e.target.textContent)) {
          alert("    .");
        }
      }
      //  
      // 
      e.stopPropagation();
    }
  };
  const onCount = event => {
    setContinent(event.key);
    setPrice(event.key * 6000);
  };
  // DB
  const transactionSuccess = () => {
    const body = {
      userFrom: userFrom,
      id: id,
      title: title,
      selectDay: selectDay,
      time: time,
      continent: Continent,
      seat: Seat,
      price: Price,
      theater: theater,
    };
    axios.post("/api/reservation", body).then(response => {
      if (response.data.success) {
        alert(" ");
        window.location.href = `${CLIENT}mymovie`;
        //  
        dispatch(addToMovie(response.data.doc._id));
      } else {
        alert(" ");
        return false;
      }
    });
  };
  const colourStyles = {
    control: styles => ({
      ...styles,
      backgroundColor: "white",
      borderRadius: "1rem",
      fontSize: "1.1rem",
      width: "250px",
      height: "35px",
      border: "1px solid #9c88ff",
      boxShadow: "0 1.5rem 2rem rgba(156, 136, 255, 0.2)",
      color: "#2e2e2e",
      fontWeight: "400",
    }),
    option: (styles, { data, isDisabled, isFocused }) => {
      return {
        ...styles,
        backgroundColor: isDisabled ? "red" : "#f7f7f7",
        color: "#151515",
        fontSize: "1.1rem",
        cursor: isDisabled ? "not-allowed" : "default",
      };
    },
  };
  const groupedOptions = [
    {
      options: Continents,
    },
  ];
  const onKaKaoPay = () => {
    var IMP = window.IMP; // 
    IMP.init("imp10561880");
    // 'iamport'   " " 
    IMP.request_pay(
      {
        pg: "inicis", // version 1.1.0 .
        pay_method: "card",
        merchant_uid: "merchant_" + new Date().getTime(),
        name: title,
        amount: Price,
        buyer_email: "iamport@siot.do",
        buyer_name: "",
        buyer_tel: "010-1234-5678",
        buyer_addr: "  ",
        buyer_postcode: "123-456",
        m_redirect_url: "https://www.yourdomain.com/payments/complete",
      },
      function (rsp) {
        if (rsp.success) {
          var msg = " .";
          msg += "ID : " + rsp.imp_uid;
          msg += " ID : " + rsp.merchant_uid;
          msg += "  : " + rsp.paid_amount;
          transactionSuccess(); //    db 
        } else {
          msg = " .";
          msg += " : " + rsp.error_msg;
        }
        alert(msg);
      }
    );
  };
  return (
    <>
      <Wrapper>
        <SelectWrapper>
          <Select
            options={groupedOptions}
            placeholder="   "
            styles={colourStyles}
            onChange={onCount}
          />
        </SelectWrapper>
        <InnerWrapper style={{ height: "300px", marginTop: "20px" }}>
          <Cover bgImage={`${IMAGE_BASE_URL}original${bgImage}`} />
        </InnerWrapper>
        <InnerWrapper
          style={{
            height: "200px",
            width:"250px",
            display: "flex",
            flexDirection: "column",
            fontSize: "17px",
            padding: "10px",
            color: "#D8D8D8",
          }}
        >
          <table style={{ height: "50%" }}>
            <tbody>
              <tr>
                <th style={{ color: "white" }}></th>
                <td>
                  {selectDay &&
                    `${selectDay.year}-${selectDay.month}-${selectDay.day}`}
                </td>
              </tr>
              <tr>
                <th style={{ color: "white" }}></th>
                <td>{time.time}</td>
              </tr>
              <tr>
                <th style={{ color: "white" }}></th>
                <td>{Continent}</td>
              </tr>
              <tr>
                <th style={{ color: "white" }}></th>
                <td style={{ width: "75%" }}>
                  {theater}
                  {Seat.map((seat, index) => {
                    if (index < Seat.length - 1) {
                      return seat + ", ";
                    } else {
                      return seat;
                    }
                  })}
                </td>
                <td></td>
              </tr>
            </tbody>
          </table>
          <hr style={{ color: "white", borderColor: "white", width: "100%" }} />
          <PriceTag>{Price}</PriceTag>
          <img
            src={require("../../img/kakaoPay.png")}
            alt="kakaoPay"
            style={{ width: "20%", height: "25px", float: "right" }}
            onClick={onKaKaoPay}
          />
        </InnerWrapper>
      </Wrapper>
      {theater % 2 === 1 ? (
        <SeatWrapper>
          <ul className="showcase">
            <li>
              <div className="seat"></div> <Small></Small>
            </li>
            <li>
              <div className="seat selected"></div> <Small></Small>
            </li>
            <li>
              <div className="seat occupied"></div> <Small></Small>
            </li>
          </ul>
          <hr
            style={{ color: "white", borderColor: "white", marginLeft: "20px" }}
          />
          <div className="container">
            <div className="screen"></div>
            {
              <div className="row">
                {SeatA.map(item => {
                  if (Distinct.includes(item.value)) {
                    return (
                      <div
                        key={item.key}
                        className="seat occupied"
                        onClick={onSeatChange}
                      >
                        {item.value}
                      </div>
                    );
                  } else {
                    return (
                      <div
                        key={item.key}
                        className="seat"
                        onClick={onSeatChange}
                      >
                        {item.value}
                      </div>
                    );
                  }
                })}
              </div>
            }
            {
              <div className="row">
                {SeatB.map(item => {
                  if (Distinct.includes(item.value)) {
                    return (
                      <div
                        key={item.key}
                        className="seat occupied"
                        onClick={onSeatChange}
                      >
                        {item.value}
                      </div>
                    );
                  } else {
                    return (
                      <div
                        key={item.key}
                        className="seat"
                        onClick={onSeatChange}
                      >
                        {item.value}
                      </div>
                    );
                  }
                })}
              </div>
            }
            {
              <div className="row">
                {SeatC.map(item => {
                  if (Distinct.includes(item.value)) {
                    return (
                      <div
                        key={item.key}
                        className="seat occupied"
                        onClick={onSeatChange}
                      >
                        {item.value}
                      </div>
                    );
                  } else {
                    return (
                      <div
                        key={item.key}
                        className="seat"
                        onClick={onSeatChange}
                      >
                        {item.value}
                      </div>
                    );
                  }
                })}
              </div>
            }
            {
              <div className="row">
                {SeatD.map(item => {
                  if (Distinct.includes(item.value)) {
                    return (
                      <div
                        key={item.key}
                        className="seat occupied"
                        onClick={onSeatChange}
                      >
                        {item.value}
                      </div>
                    );
                  } else {
                    return (
                      <div
                        key={item.key}
                        className="seat"
                        onClick={onSeatChange}
                      >
                        {item.value}
                      </div>
                    );
                  }
                })}
              </div>
            }
            {
              <div className="row">
                {SeatE.map(item => {
                  if (Distinct.includes(item.value)) {
                    return (
                      <div
                        key={item.key}
                        className="seat occupied"
                        onClick={onSeatChange}
                      >
                        {item.value}
                      </div>
                    );
                  } else {
                    return (
                      <div
                        key={item.key}
                        className="seat"
                        onClick={onSeatChange}
                      >
                        {item.value}
                      </div>
                    );
                  }
                })}
              </div>
            }
            {
              <div className="row">
                {SeatF.map(item => {
                  if (Distinct.includes(item.value)) {
                    return (
                      <div
                        key={item.key}
                        className="seat occupied"
                        onClick={onSeatChange}
                      >
                        {item.value}
                      </div>
                    );
                  } else {
                    return (
                      <div
                        key={item.key}
                        className="seat"
                        onClick={onSeatChange}
                      >
                        {item.value}
                      </div>
                    );
                  }
                })}
              </div>
            }
            {
              <div className="row">
                {SeatG.map(item => {
                  if (Distinct.includes(item.value)) {
                    return (
                      <div
                        key={item.key}
                        className="seat occupied"
                        onClick={onSeatChange}
                      >
                        {item.value}
                      </div>
                    );
                  } else {
                    return (
                      <div
                        key={item.key}
                        className="seat"
                        onClick={onSeatChange}
                      >
                        {item.value}
                      </div>
                    );
                  }
                })}
              </div>
            }
          </div>
        </SeatWrapper>
      ) : (
        <SeatWrapper>
          <ul className="showcase">
            <li>
              <div className="seat"></div> <Small></Small>
            </li>
            <li>
              <div className="seat selected"></div> <Small></Small>
            </li>
            <li>
              <div className="seat occupied"></div> <Small></Small>
            </li>
          </ul>
          <hr
            style={{ color: "white", borderColor: "white", marginLeft: "20px" }}
          />
          <div className="container">
            <div className="screen"></div>
            {
              <div className="row">
                {SeatA2.map(item => {
                  if (Distinct.includes(item.value)) {
                    return (
                      <div
                        key={item.key}
                        className="seat occupied"
                        onClick={onSeatChange}
                      >
                        {item.value}
                      </div>
                    );
                  } else {
                    return (
                      <div
                        key={item.key}
                        className="seat"
                        onClick={onSeatChange}
                      >
                        {item.value}
                      </div>
                    );
                  }
                })}
              </div>
            }
            {
              <div className="row">
                {SeatB2.map(item => {
                  if (Distinct.includes(item.value)) {
                    return (
                      <div
                        key={item.key}
                        className="seat occupied"
                        onClick={onSeatChange}
                      >
                        {item.value}
                      </div>
                    );
                  } else {
                    return (
                      <div
                        key={item.key}
                        className="seat"
                        onClick={onSeatChange}
                      >
                        {item.value}
                      </div>
                    );
                  }
                })}
              </div>
            }
            {
              <div className="row">
                {SeatC2.map(item => {
                  if (Distinct.includes(item.value)) {
                    return (
                      <div
                        key={item.key}
                        className="seat occupied"
                        onClick={onSeatChange}
                      >
                        {item.value}
                      </div>
                    );
                  } else {
                    return (
                      <div
                        key={item.key}
                        className="seat"
                        onClick={onSeatChange}
                      >
                        {item.value}
                      </div>
                    );
                  }
                })}
              </div>
            }
            {
              <div className="row">
                {SeatD2.map(item => {
                  if (Distinct.includes(item.value)) {
                    return (
                      <div
                        key={item.key}
                        className="seat occupied"
                        onClick={onSeatChange}
                      >
                        {item.value}
                      </div>
                    );
                  } else {
                    return (
                      <div
                        key={item.key}
                        className="seat"
                        onClick={onSeatChange}
                      >
                        {item.value}
                      </div>
                    );
                  }
                })}
              </div>
            }
            {
              <div className="row">
                {SeatE2.map(item => {
                  if (Distinct.includes(item.value)) {
                    return (
                      <div
                        key={item.key}
                        className="seat occupied"
                        onClick={onSeatChange}
                      >
                        {item.value}
                      </div>
                    );
                  } else {
                    return (
                      <div
                        key={item.key}
                        className="seat"
                        onClick={onSeatChange}
                      >
                        {item.value}
                      </div>
                    );
                  }
                })}
              </div>
            }
            {
              <div className="row">
                {SeatF2.map(item => {
                  if (Distinct.includes(item.value)) {
                    return (
                      <div
                        key={item.key}
                        className="seat occupied"
                        onClick={onSeatChange}
                      >
                        {item.value}
                      </div>
                    );
                  } else {
                    return (
                      <div
                        key={item.key}
                        className="seat"
                        onClick={onSeatChange}
                      >
                        {item.value}
                      </div>
                    );
                  }
                })}
              </div>
            }
            {
              <div className="row">
                {SeatG2.map(item => {
                  if (Distinct.includes(item.value)) {
                    return (
                      <div
                        key={item.key}
                        className="seat occupied"
                        onClick={onSeatChange}
                      >
                        {item.value}
                      </div>
                    );
                  } else {
                    return (
                      <div
                        key={item.key}
                        className="seat"
                        onClick={onSeatChange}
                      >
                        {item.value}
                      </div>
                    );
                  }
                })}
              </div>
            }
          </div>
        </SeatWrapper>
      )}
    </>
  );
}
export default Booking;
